<template>
  <div class="login_form_f ">
    <div class="login_form">
      <div class="text-danger text-center">{{errors}}</div>
        <form @submit.prevent="signUp()">
          <div class="form_input">
              <label class="form_label">Login</label>
            <div class="input_f">
              <input  type="text" v-model="username">
            </div>
          </div>
          <div class="form_input">
            <label class="form_label">
              <div class="password_show">
                <div>parol</div>
                <div @click="show_password()" class="show_password">
                  <span>
                   <b-icon icon="eye-fill" aria-hidden="true" v-if="password"></b-icon>
                   <b-icon icon="eye-slash-fill" aria-hidden="true" v-else></b-icon>
                  </span>
                </div>
              </div>
            </label>
            <div class="input_f">
              <input  type="password" v-model="password_name" id="password">
            </div>
          </div>
          <div class="form_input">
            <button>Kirish</button>
          </div>
        </form>
    </div>
  </div>
</template>
<script>
export default {
  data(){
    return{
      password:false,
      password_name:'',
      username:'',
      errors:''
    }
  },
  methods:{
    show_password(){
      const passwordField = document.querySelector('#password')
      if (passwordField.getAttribute('type') === 'password') passwordField.setAttribute('type', 'text')
      else passwordField.setAttribute('type', 'password')
      this.password=!this.password
    },
    signUp(){
          this.$axiosDefault.post('/api/token/',{
            "username":this.username,
            "password":this.password_name
          })
          .then(res=>{
            localStorage.setItem('access',res.data.access)
            localStorage.setItem('access',res.data.refresh)
            localStorage.setItem('avatar',res.data.avatar)
            localStorage.setItem('email',res.data.email)
            localStorage.setItem('full_name',res.data.full_name)
            this.$router.push('/')
          })
        .catch(()=>{
          this.errors="username yoki parol xato"
        })
    }
  }

}
</script>
<style>
.login_form_f{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}
  .login_form{
    width: 584px;
    height: 377px;
    background: #FFFFFF;
    border: 1px solid rgba(102, 102, 102, 0.25);
    border-radius: 12px;
    padding: 32px;
  }
  .form_input{
    width: 100%;
    margin-bottom: 40px;
  }
  .form_label{
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #666666;
    width: 100%;
  }
  .input_f{
    border: 1px solid rgba(102, 102, 102, 0.35);
    border-radius: 5px;
    width: 100%;
    height: 46px;
    margin-top: 10px;
    padding: 10px;
  }
  .input_f input{
    padding: 0;
    width: 100%;
    height: 100%;
    outline: none;
    border: 0;
    font-size: 30px;
  }
  .form_input button{
    background: #5A75FF;
    border-radius: 10px;
    width: 100%;
    height: 64px;
    border: 0;
    outline: none;
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 500;
    font-size: 22px;
    line-height: 33px;
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    color: #FFFFFF;
  }
  .password_show{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  .show_password{
    background: #C1C7CD;
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    border-radius: 5px;
  }
</style>
